.tsh1 {
  font-family: "Josefin Sans semi bold";
  color: #F4989C;
  display: flex;
  justify-content: center;
  margin-top: 70px;
}

.tsbody {
  background-color: #D6F6DD;
}

.tsform {
  font-family: "Josefin Sans regular";
  float: none;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #DAC4F7;
  border-radius: 15px;
  padding: 20px;
  background-color: #DAC4F7;
  display: inline-block;
}

.tspreviewcontainer {
  margin-left: -10px;
  margin-right: auto;
  border: 1px solid white;
  background-color: white;
  padding: 10px;
  display: inline-block;
}

.tssubcontainer {
  padding: 10px;
  display: inline-block;
  justify-content: center;
}

.tscontainer {
  display: flex;
  justify-content: center;
}

#previewtext{
  font-family: "Josefin Sans semi bold";
  margin-bottom: 5px;

}

